<template>
  <div>
    <template>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1"
                          step="1">Edit General</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step :complete="e1 > 2"
                          step="2">Staff Propety Group</v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <editGeneral @next='nextstep'></editGeneral>
          </v-stepper-content>

          <v-stepper-content step="2">
            <staffGroup @laststep='laststep'
                        @savesuccess='savesuccess'></staffGroup>
          </v-stepper-content>

        </v-stepper-items>
      </v-stepper>
    </template>
  </div>
</template>

<script>
import editGeneral from '../modules/editGeneral'
import staffGroup from '../modules/staffGroup'
export default {
  components: {
    editGeneral,
    staffGroup
  },
  props: {},
  data () {
    return {
      e1: 1,
      list: {}//editGeneral基本用户输入数据
    };
  },
  created () { },
  mounted () {
    // console.log('渲染', this.$route, this.$router);
  },
  methods: {
    nextstep (num, list) { //下一步  并获取用户输入数据
      this.e1 = num
      this.list = list
      console.log(list);
    },
    laststep (v) { //上一步
      this.e1 = v
    },
    savesuccess (val) {
      // console.log('属性列表数据', val);
    }
  },
  computed: {},
  watch: {},
};
</script>

<style scoped>
.v-stepper__content {
  padding: 0;
}
</style>
